/* Fonts and reset */
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&display=swap');

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #f9f7f6;
  color: #333;
}

/* Top contact bar */
.top-bar {
  background-color: #222;
  color: #fff;
  padding: 8px 16px;
  text-align: right;
  font-size: 0.85rem;
}

/* Navbar */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px 40px;
  background-color: #fff;
  border-bottom: 1px solid #eee;
}

.logo {
  display: flex;
  flex-direction: column;
  line-height: 1.2;
}

.logo .initials {
  font-size: 2rem;
  font-weight: 700;
  color: #d5a373;
}

.logo .name {
  font-size: 1.2rem;
  font-weight: 600;
}

.logo .subtitle {
  font-size: 0.85rem;
  color: #888;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 24px;
  padding: 0;
  margin: 0;
}

.nav-links li {
  cursor: pointer;
  font-weight: 500;
  transition: color 0.3s ease;
}

.nav-links li:hover {
  color: #d5a373;
}

/* âœ… Add this */
.nav-links a {
  text-decoration: none;
  color: inherit;
}

/* Hero Section */
.hero {
  padding: 40px;
  text-align: center;
}

.image-row {
  display: flex;
  justify-content: center;
  gap: 16px;
  margin-bottom: 20px;
}

.image-row img {
  width: 200px;
  height: auto;
  border-radius: 12px;
  object-fit: cover;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.hero h2 {
  font-size: 2rem;
  font-weight: 600;
}

.about {
  background-color: #f3e6e1;
  padding: 60px 20px;
}

.about-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 40px;
}

.about-text {
  flex: 1;
  max-width: 500px;
}

.about-text h2 {
  font-size: 26px;
  color: #7e4f39;
  margin-bottom: 20px;
}

.about-text p {
  font-size: 16px;
  color: #555;
  line-height: 1.6;
}

.about-btn {
  margin-top: 20px;
  background-color: #7e4f39;
  color: #fff;
  border: none;
  padding: 10px 24px;
  font-size: 14px;
  border-radius: 30px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.about-btn:hover {
  background-color: #5a3727;
}

.about-images {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.img-main {
  width: 220px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
}

.img-small {
  width: 160px;
  border-radius: 8px;
  align-self: flex-end;
  box-shadow: 0 4px 8px rgba(0,0,0,0.08);
}

/* Services Section */
.services {
  padding: 60px 40px;
  background-color: #fefefe;
  text-align: center;
}

.services h2 {
  font-size: 2rem;
  margin-bottom: 40px;
}

.service-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
  gap: 24px;
}

.service-list div {
  background: #fff;
  padding: 12px;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.05);
}

.service-list img {
  width: 100%;
  height: 160px;
  object-fit: cover;
  border-radius: 8px;
}

.services button {
  margin-top: 32px;
  background-color: #d5a373;
  color: white;
  padding: 12px 24px;
  font-weight: 600;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

/* Testimonials */
.testimonials {
  background-color: #f3f3f3;
  padding: 60px 40px;
  display: flex;
  flex-direction: column;
  gap: 32px;
  align-items: center;
}

.testimonial {
  text-align: center;
  max-width: 600px;
}

.testimonial img {
  width: 90px;
  height: 90px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 12px;
}

blockquote {
  font-style: italic;
  color: #555;
}

.footer {
  background-color: #e7dbd3;
  padding: 40px 20px;
  text-align: center;
  font-size: 14px;
  color: #333;
}

.footer-container {
  max-width: 1000px;
  margin: 0 auto;
}

.footer-logo .initials {
  font-size: 30px;
  font-weight: bold;
  color: #bf9079;
}

.footer-logo .name {
  font-size: 18px;
  font-weight: 600;
}

.footer-logo .subtitle {
  font-size: 14px;
  color: #555;
}

.footer-description {
  margin: 15px 0;
  font-size: 13px;
  color: #444;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.footer-links,
.footer-socials {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin: 15px 0;
  flex-wrap: wrap;
}

.footer-links a,
.footer-socials a {
  text-decoration: none;
  color: #333;
  font-weight: 500;
  transition: color 0.3s ease;
}

.footer-links a:hover,
.footer-socials a:hover {
  color: #bf9079;
}

.footer-newsletter {
  margin-top: 20px;
}

.footer-newsletter input {
  padding: 8px;
  width: 220px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 10px;
}

.footer-newsletter button {
  background-color: #bf9079;
  color: white;
  border: none;
  padding: 8px 14px;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.footer-newsletter button:hover {
  background-color: #a36e58;
}

.footer-credit {
  margin-top: 30px;
  font-size: 12px;
  color: #666;
}
/* Mobile Responsive Styling */
@media (max-width: 768px) {
  /* Navbar */
  .navbar {
    flex-direction: column;
    align-items: flex-start;
    padding: 16px;
  }

  .nav-links {
    flex-direction: column;
    gap: 12px;
    margin-top: 10px;
  }

  /* Hero Section */
  .image-row {
    flex-direction: column;
    align-items: center;
  }

  .image-row img {
    width: 90%;
    max-width: 300px;
  }

  .hero h2 {
    font-size: 1.5rem;
    padding: 0 10px;
  }

  /* About Section */
  .about-wrapper {
    flex-direction: column;
    text-align: center;
    align-items: center;
  }

  .about-images {
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
  }

  .img-main,
  .img-small {
    width: 45%;
    max-width: 200px;
  }

  .about-text h2 {
    font-size: 22px;
  }

  .about-text p {
    font-size: 15px;
  }

  .about-btn {
    width: 80%;
    max-width: 240px;
  }

  /* Services Section */
  .services {
    padding: 40px 20px;
  }

  .service-list {
    grid-template-columns: 1fr;
  }

  .service-list div {
    padding: 16px;
  }

  .services button {
    width: 90%;
    max-width: 250px;
  }

  /* Testimonials */
  .testimonials {
    padding: 40px 20px;
  }

  .testimonial img {
    width: 70px;
    height: 70px;
  }

  blockquote {
    font-size: 14px;
    line-height: 1.5;
  }

  /* Footer */
  .footer-links,
  .footer-socials {
    flex-direction: column;
    gap: 10px;
  }

  .footer-newsletter input {
    width: 100%;
    margin: 10px 0;
  }

  .footer-newsletter button {
    width: 100%;
  }
}
